<script>
    import Layout from '../c8s/Layout.svelte'
    import BusyScreen from '../c8s/BusyScreen.svelte'
    import {onMount} from 'svelte'
    import Api from '../service/api-service'
    import CenteredFlex from "../c8s/CenteredFlex.svelte";

    export let params;

    let project = {};
    let loading = true;

    onMount(async () => {
        let api = new Api();
        project = await api.getProject(params.id);
        loading = false;
    })

    let debug = false
</script>

<Layout>
    <div class="relative grid grid-cols-6 grid-rows-2 gap-1 mt-2">
        <BusyScreen loading="{loading}"/>
        <div class:bg-red-200={debug} class="flex justify-center">
            <a href="#/projects" class="md:hidden flex flex-col justify-center">
                <img class="w-8" src="/img/chevron-left.svg" alt="back">
            </a>
        </div>
        <div class:bg-red-200={debug}
             class="col-span-4 md:col-span-4 items-center w-full flex justify-center items-center">
            <div class="text-center text-lg">{project.title}</div>
        </div>
        <div class:bg-red-200={debug} class="flex justify-center">
            <a href="#/project-edit/{project.id}" class="flex items-center border-black border-b-2 border-dotted">
                <img class="w-6" src="/img/edit.svg" alt="edit-project">
                <div class="hidden md:inline-block">Edit project</div>
            </a>

        </div>

        <div class:bg-red-200={debug}
             class="col-span-6 flex justify-center items-center text-sm">{project.description}</div>
    </div>
</Layout>